import { useState } from "react";
import ReactMarkdown from "react-markdown";
import remarkGfm from "remark-gfm";
import rehypeRaw from "rehype-raw";
import ittrip from "../css/img/pdf/ItTrip.pdf";
import iwc from "../css/img/pdf/iwc.pdf"
const Project = () => {
  const openPdfFile = (type) => {
    const urlMap = {
      ittrip: ittrip,
      iwc: iwc
    };
    const pdfUrl = urlMap[type];
    window.open(pdfUrl, '_blank')
  }

  const [modalOpen, setModalOpen] = useState(false);
  const [readmeContent, setReadmeContent] = useState("");



  const loadReadme = async () => {
    try {
      const response = await fetch(`${process.env.PUBLIC_URL}/README.md`); // public   
      const text = await response.text();
      setReadmeContent(text);
    } catch (error) {
      console.error("Error loading README file:", error);
    }
  };

  const openModal = () => {
    loadReadme(); // README  
    setModalOpen(true);
  };

  //  
  const closeModal = () => {
    setModalOpen(false);
  };


  return (
    <section id="project" className="project section">
      <div className="project_content content">
        <div className="section_title">
          <h3>PROJECTS</h3>
        </div>
        <div className="project_list content">
          <div className="project_detail">
            <h4>IT Trip ()</h4>
            <div className="project_period"> </div>
            <div className="project_summary">
              <h5>React, Spring Boot      AWS </h5>
              <ul className="projectSum_ul">
                <li>     </li>
                <li>  , ,       </li>
                <li>,    </li>
                <li>        </li>
              </ul>
            </div>
            <div className="project_skills">
              HTML, CSS, React, Java, Spring boot,AWS,EC2,RDS
            </div>
            <div className="project_pdf project_button">
              <button onClick={() => openPdfFile('ittrip')}></button>
            </div>
            <div className="project_url project_button">
              <a href="http://ittrip.site"></a>
            </div>
            <div className="project_readMe project_button">
              <button onClick={openModal}>README</button>
            </div>
          </div>
          <div className="project_detail">
            <h4>Movie ( )</h4>
            <div className="project_period"> </div>
            <div className="project_summary">
              <h5>Vanilla JS      </h5>
              <ul className="projectSum_ul">
                <li>OTT     </li>
                <li>   </li>
                <li>     </li>
              </ul>
            </div>
            <div className="project_skills">
              Vanilla JavaScript
            </div>
            <div className="project_pdf project_button">
              <button onClick={() => openPdfFile('')}></button>
            </div>
            <div className="project_url project_button">
              <a href="https://hwan1002.github.io/movie/movie.html"></a>
            </div>
            <div className="project_readMe project_button">
              <button onClick={openModal}>README</button>
            </div>
          </div>
          <div className="project_detail">
            <h4>IWC ( )</h4>
            <div className="project_period"> </div>
            <div className="project_summary">
              <h5>HTML, CSS, JavaScript, Jquery   </h5>
              <ul className="projectSum_ul">
                <li> IWC   </li>
                <li>  PC,Table,Mobile  UI/UX  </li>
                <li>     UP!</li>
              </ul>
            </div>
            <div className="project_skills">
              HTML, CSS, React, JavaScript, Jquery
            </div>
            <div className="project_pdf project_button">
              <button onClick={() => openPdfFile('iwc')}></button>
            </div>
            <div className="project_url project_button">
              <a href="http://ghkstjr12.dothome.co.kr/iwc/index.html"></a>
            </div>
            <div className="project_readMe project_button">
              <button onClick={openModal}>README</button>
            </div>
          </div>
          {modalOpen && (
          <div className="modal-backdrop" onClick={(e) => {
            // (Backdrop)    
            if (e.target === e.currentTarget) {
              closeModal();
            }
          }}>
            <div className="modal">
              {/*   */}
              <div className = "modal-closeBt">
                README.md
              <button className="close-btn" onClick={closeModal} aria-label="Close">
                &times;     
              </button>
              </div>
              {/* README   */}
              {readmeContent ? (
                <div className="modalContent">
                <ReactMarkdown
                  remarkPlugins={[remarkGfm]}
                  rehypePlugins={[rehypeRaw]} // HTML  
                >
                  {readmeContent}
                </ReactMarkdown>
                </div>
              ) : (
                <p>Loading...</p>
              )}
            </div>
          </div>
        )}
        </div>
      </div>
    </section>
    
  )
}

export default Project;